<template>
  <div id="total">
    <Row>
      <Col :xs="12" class="header">
        <h2>我的统计</h2>
        <Table stripe :columns="columns1" :data="data1" :loading="loadTable" class="my"></Table>
        <h2>VIP数量统计</h2>
        <Table stripe :columns="columns2" :data="data2" size="small"></Table>

        <svg class="waves" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
             viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
          <defs>
            <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"/>
          </defs>
          <g class="parallax">
            <use xlink:href="#gentle-wave" x="48" y="0" fill="rgba(255,255,255,0.7"/>
            <use xlink:href="#gentle-wave" x="48" y="3" fill="rgba(255,255,255,0.5)"/>
            <use xlink:href="#gentle-wave" x="48" y="5" fill="rgba(255,255,255,0.3)"/>
            <use xlink:href="#gentle-wave" x="48" y="7" fill="#fff"/>
          </g>
        </svg>
      </Col>
    </Row>
  </div>
</template>

<script>
    import mixins from '../mixins'
    import * as api from '../api'
    import * as types from '../store/mutation-types'

    export default {
        name: "total",
        mixins: [mixins],
        data() {
            return {
                loadTable:false,
                columns1:[
                    {title:'钱包地址',key:'addr',width: 400},
                    {title:'VIP',key:'grade',width: 100},
                    {title:'我的总投资',key:'invest',width: 120},
                    {title:'我的总收益',key:'totalCash',width: 120},
                    {title:'全球收益',key:'angelAmount',width: 120},
                    {title:'是否大使',key:'isAngel',width: 100},
                ],
                columns2:[
                    {title:'等级',key:'grade'},
                    {title:'数量',key:'count'}
                ],
                data2:[],
            }
        },
        filters: {
            flex(e, r) {
                if (e) {
                    return e.toFixed(r)
                } else {
                    return e;
                }
            },
            subString(e, r) {
                return e.substring(0, 16) + '...' + e.substring(e.length - r)
            }
        },
        computed:{
            data1(){
                let a=[];
                a[0]={
                    addr:this.user.addr,
                    grade:this.user.grade,
                    invest:this.user.invest+' ETH',
                    totalCash:this.user.totalCash+' ETH',
                    angelAmount:this.user.angelAmount+' ETH',
                    isAngel:this.user.isAngel?'是':'否'
                };
                return a
            }
        },
        mounted() {
            // 初始化
            let that = this;
            this.sockets.subscribe('reload', async data => {
                if (data.reload) {
                    that.init();
                    let user = await api.getUser();
                    that.$store.commit(types.UPDATE_USER, user.user);
                }
            });
            this.getGradeCount();
        },
        methods: {
            // 初始化
            init() {
                let that = this;
                this.$store.dispatch('initService');
                this.countTime();
                setTimeout(function () {
                    that.$store.dispatch('newContract');
                }, 5000);
            },
            async getGradeCount(){
                let g=await api.getGradeCount();
                this.data2=g.data;
            }
        }
    }
</script>

<style lang="less">
  #total {
    background: rgb(30, 25, 49);

    h2{
      font-size: 1.2rem;
      padding: 1rem;
    }
    .my{
      td div span{
        display: block;
        white-space: nowrap;
      }

    }
    .header {
      position: relative;
      width: 100%;
      height: 100vh;

      .ivu-card {
        background: none;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }

    }

  }

  .waves {
    position: absolute;
    width: 100%;
    height: 15vh;
    margin-bottom: -7px; /*Fix for safari gap*/
    min-height: 100px;
    max-height: 150px;
    bottom: 0;
  }

  .content {
    position: relative;
    height: 20vh;
    text-align: center;
    background-color: white;
  }

  /* Animation */

  .parallax > use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
  }

  .parallax > use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
  }

  .parallax > use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
  }

  .parallax > use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
  }

  .parallax > use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
  }

  @keyframes move-forever {
    0% {
      transform: translate3d(-90px, 0, 0);
    }
    100% {
      transform: translate3d(85px, 0, 0);
    }
  }

  /*Shrinking for mobile*/
  @media (max-width: 768px) {
    .waves {
      height: 60px;
      min-height: 60px;
    }

    .content {
      height: 30vh;
    }

    h1 {
      font-size: 24px;
    }
  }
</style>



